.body {
  background-color: #000;
}

h1,
h2,
h3,
h4 {
  color: #fff;
}

a {
  color: #fff;
  text-decoration: none;
}

p {
  margin: 0.5rem 0;
}

img {
  width: 100%;
}

.showcase {
	width: 100%;
	height: 93vh;
	position: relative;
	background: url("https://i.ibb.co/vXqDmnh/background.jpg") no-repeat center
    center/cover;
	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
		background: rgba(0, 0, 0, 0.3);
		box-shadow: inset 120px 100px 250px #000000, inset -120px -100px 250px #000000;
	}
}
.showcase-top {
	position: relative;
	z-index: 2;
	height: 90px;
	img {
		width: 6.75rem;
		position: absolute;
		top: 50%;
		left: 10%;
		transform: translate(-50%, -50%);
		margin-left: 0;
	}
	a {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(-50%, -50%);
	}
}
.showcase-content {
	position: relative;
	z-index: 2;
	width: 65%;
	margin: auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin-top: 9rem;
	h1 {
		font-weight: 600;
		font-size: 4.5rem;
		line-height: 1.1;
		margin: 0 0 2rem;
	}
	p {
		text-transform: uppercase;
		color: #fff;
		font-weight: 400;
		font-size: 1.9rem;
		line-height: 1.25;
		margin: 0 0 2rem;
	}
}


.btn-header {
  display: inline-block;
  background: #e50914;
  color: #fff;
  padding: 0.8rem 1.3rem;
  font-size: 1rem;
  text-align: center;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.45);
  border-radius: 2px;
}

.btn:hover {
  opacity: 0.9;
}

.btn-rounded {
  border-radius: 5px;
}

.btn-xl {
  font-size: 1rem;
  padding: 0.8rem 2.1rem;
}

.btn-icon {
  margin-left: 1rem;
}

.part1 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 2rem 4rem;
}

.part1 .talk {
  width: 40%;
}

.part1 .talk h1 {
  font-size: 3.3rem;
  letter-spacing: 1px;
  padding-bottom: 10px;
  color: #fff;
}

.part1 .talk h1 {
  font-size: 1.7rem;
  color: #fff;
}

.part1 .img {
  width: 50%;
}

.part1 .img  img{
  width: 100%;
}

@media (max-width: 960px) {
  .showcase {
    height: 70vh;
  }

  .showcase-top img {
    top: 30%;
    left: 5%;
    transform: translate(0);
  }

  .showcase-content h1 {
    font-size: 3.7rem;
    line-height: 1;
  }

  .showcase-content p {
    font-size: 1.5rem;
  }

  .btn-xl {
    padding: 0.9rem 2.1rem;
  }
}



@media (max-width: 700px) {
  .showcase::after {
    background: rgba(0, 0, 0, 0.6);
    box-shadow: inset 80px 80px 150px #000000, inset -80px -80px 150px #000000;
  }

  .showcase-content h1 {
    font-size: 2.5rem;
    line-height: 1;
  }

  .showcase-content p {
    font-size: 1rem;
  }

  .btn-xl {
    font-size: 1rem;
    padding: 1.2rem 1.6rem;
    text-transform: uppercase;
  }
}

@media (max-height: 600px) {
  .showcase-content {
    margin-top: 3rem;
  }
}


